import { DirektoriPegawaiService, DirektoriPegawaiMasterService } from 'src/app/sdk/hr/services';
import { Pegawai } from './../../../../sdk/hr/models/pegawai';
import { Component, OnInit } from '@angular/core';
import { UserUsersService } from 'src/app/sdk/user/services';
import { Users } from 'src/app/sdk/user/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(

        private userUsersService: UserUsersService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router

    ) { }

    dataUser: Users = {}
    listPegawai: Pegawai [] = []
    ngOnInit() {
        this.form()
        this.getPegawai()
    }

    getPegawai() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(data => {
            this.listPegawai = data.data
        })
    }


    dataForm: FormGroupTypeSafe<Users>
    form() {
        this.dataForm = this.fb.group<Users>({
            name: new FormControl('', [Validators.required]),
            email: new FormControl('', [Validators.required]),
            password: new FormControl('', [Validators.required]),
            id_pegawai: new FormControl('', [Validators.required]),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataUser = this.dataForm.value
        this.userUsersService
            .postUserUsers(this.dataUser)
            .subscribe(
                data => {

                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }



    goToBack() {

        this.router.navigate(['users/pengguna/list'])
    }


}
