import { Component, OnInit } from '@angular/core';
import { UserUsersService, UserGroupService } from 'src/app/sdk/user/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Users, Group } from 'src/app/sdk/user/models';
import { FormControl, Validators } from '@angular/forms';
import { Pegawai } from 'src/app/sdk/hr/models';
import { DirektoriPegawaiService } from 'src/app/sdk/hr/services';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private userGroupService: UserGroupService,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private userUsersService: UserUsersService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,

    ) { }

    user: Users = {}
    id: string = ''

    ngOnInit() {
        this.id = this.activatedRoute.snapshot.paramMap.get('id')
        this.getData();
        this.getGroup();
    }

    getData() {
        this.spinner.show()
        this.userUsersService.getUserUsersId(this.id).subscribe(
            data => {
                this.spinner.hide()
                this.user = data.data
                this.user.password = null
            }
        )
    }

    listDataGroup: Group[] = []
    getGroup() {
        this.spinner.show()
        this.userGroupService.getUserGroups().subscribe(
            data => {
                this.spinner.hide()
                this.listDataGroup = data.data
            }
        )
    }
    password: boolean = false;


    toglePassword() {

        this.user.password = null;

    }


    simpan() {
        this.spinner.show()
        if (!this.user.email) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.userUsersService
            .putUserUsersId({ id: this.id, body: this.user })
            .subscribe(
                data => {

                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }



    goToBack() {

        this.router.navigate(['users/pengguna/list'])
    }

}
