import { Pegawai } from 'src/app/sdk/hr/models';
import { DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { UserUsersService, UserGroupService } from 'src/app/sdk/user/services';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { Users, Group } from 'src/app/sdk/user/models';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private userUsersService: UserUsersService,
        private userGroupService: UserGroupService,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }

    dataUser: Users = {}
    dataPegawai: Pegawai = {}
    dataGroup: Group = {}


    id: string = ''

    ngOnInit() {
        this.id = this.routerActive.snapshot.paramMap.get('id')
        this.getModule();
    }
    getModule() {
        this.spinner.show()
        this.userUsersService.getUserUsersId(this.id).subscribe(
            response => {
                this.dataUser = response.data
                this.spinner.hide()

                this.getPegawai(this.dataUser.id_pegawai);
                this.getGroup(this.dataUser.id_group);
            }
        )

    }

    getPegawai(id){
        this.direktoriPegawaiService.getDirektoriPegawaisId(id).subscribe(
            data=> {
                this.dataPegawai = data.data
            }
        )
    }

    getGroup(id){
        this.userGroupService.getUserGroupsId(id).subscribe(
            data=> {
                this.dataGroup = data.data
            }
        )
    }


    goToBack() {

        this.router.navigate(['/users/pengguna/list'])
    }

}
